import {getBalance, getBill, checkTaskList} from '../../api/api.js';
export default {
	data() {
		return {
			assetInfo: '',
			listInfo: '',
			labelList: 'budget',
			budgetText: 'black',
			taskText: 'gray'
		}
	},
	methods: {
		getBalanceData() {
			getBalance().then(data => {
				this.assetInfo = data;
			});
		},
		getBillData() {
			getBill().then(data => {
				this.listInfo = '';
				this.listInfo = data;
				// console.log(this.listInfo);
			});
		},
		getTaskData() {
			checkTaskList().then(data => {
				this.listInfo = '';
				this.listInfo = data;
				// console.log(this.listInfo);
			});
		},
		changeList(chooseItem) {
			if(chooseItem == 'budget') {
				this.labelList = 'budget';
				this.budgetText = 'black';
				this.taskText = 'gray';
				this.getBillData();
			} else {
				this.labelList = 'task';
				this.budgetText = 'gray';
				this.taskText = 'black';
				this.getTaskData();
			}
		}
	},
	onLoad() {
		this.getBalanceData();
		this.getBillData();
	}
}
